import React, { useContext, useState } from "react";
import {
  Box,
  Button,
  Divider,
  IconButton,
  Popover,
  Switch,
  SxProps,
  Theme,
} from "@mui/material";
import {
  Settings as SettingsIcon,
  FormatShapes as FormatShapesIcon,
  SaveAsOutlined as SaveAsOutlinedIcon,
  SaveOutlined as SaveOutlinedIcon,
  DocumentScannerOutlined as DocumentScannerOutlinedIcon,
  Attribution as AttributionIcon,
} from "@mui/icons-material";
import AppContext from "../context/AppContext";
import PagePciker from "../components/dialog/PagePicker";

const FtnRow = () => {
  const [anchorEl, setAnchorEl] = useState<HTMLButtonElement | null>(null);
  const {
    mode,
    setMode,
    pageStack,
    slicePageStack,
    pages,
    toggleItemEditor,
    loadPage,
    isPageDirty,
    curPage,
    savePage,
    togglePageEditor,
  } = useContext(AppContext);

  return (
    <Box sx={rootSx}>
      <Box sx={breadcrumbSx}>
        {mode === "view" &&
          pageStack.map((pageId, idx) => (
            <React.Fragment key={`bc-${idx}`}>
              {idx !== 0 && <>/</>}
              <Button key={`bc-${idx}`} onClick={() => slicePageStack(idx)}>
                {pages[pageId].name}
              </Button>
            </React.Fragment>
          ))}
        {mode === "edit" && (
          <Box sx={{ width: 250 }}>
            <PagePciker
              label=""
              value={curPage.id}
              onChange={(v) => loadPage(v)}
              disableClearable
            />
          </Box>
        )}
      </Box>
      <Box sx={{ display: "flex", justifyContent: "flex-end", flex: 1 }}>
        {mode === "edit" && isPageDirty && (
          <>
            <IconButton
              size="small"
              color="primary"
              onClick={() => savePage(curPage)}
            >
              <SaveOutlinedIcon />
            </IconButton>
            <IconButton
              size="small"
              color="primary"
              onClick={() =>
                savePage({ ...curPage, name: curPage.name + " - Copy" }, true)
              }
            >
              <SaveAsOutlinedIcon />
            </IconButton>
          </>
        )}
        <Button
          size="small"
          disableRipple
          onClick={(e) => setAnchorEl(e.currentTarget)}
        >
          <SettingsIcon />
        </Button>
      </Box>
      <Popover
        open={Boolean(anchorEl)}
        anchorEl={anchorEl}
        onClose={() => setAnchorEl(null)}
        slotProps={{
          paper: {
            sx: menuSx,
          },
        }}
        anchorOrigin={{
          vertical: "bottom",
          horizontal: "left",
        }}
      >
        <Box
          sx={{ cursor: "pointer" }}
          onClick={() => {
            togglePageEditor();
            setAnchorEl(null);
          }}
        >
          編輯此頁
          <DocumentScannerOutlinedIcon />
        </Box>
        <Box>
          編輯詞語排版
          <Switch
            checked={mode === "edit"}
            onChange={({ target: { checked } }) => {
              setMode(checked ? "edit" : "view");
              setAnchorEl(null);
            }}
            size="small"
          />
        </Box>
        <Box
          sx={{ cursor: "pointer" }}
          onClick={() => {
            toggleItemEditor();
            setAnchorEl(null);
          }}
        >
          詞語編輯器
          <FormatShapesIcon />
        </Box>
        <Divider sx={{ width: "100%" }} />
        <Box
          sx={{ cursor: "pointer" }}
          onClick={() => {
            window.open("/attribution.html", "_blank");
            setAnchorEl(null);
          }}
        >
          版權相關
          <AttributionIcon />
        </Box>
      </Popover>
    </Box>
  );
};

export default FtnRow;

const rootSx: SxProps<Theme> = {
  display: "flex",
  justifyContent: "space-between",
  p: 2,
  background: (t) => t.palette.background.default,
  "& button": {
    fontSize: "0.8em",
  },
};

const breadcrumbSx: SxProps<Theme> = {
  display: "flex",
  "& button": {
    textTransform: "none",
  },
  alignItems: "center",
};

const menuSx: SxProps<Theme> = {
  display: "flex",
  flexDirection: "column",
  alignItems: "center",
  "& > div": {
    display: "flex",
    width: "20ch",
    justifyContent: "space-between",
    alignItems: "center",
    px: 1,
  },
  p: 1,
  gap: 1,
};
